import './Circles.css';

export default function Circles(props) {
    return (
        <div className="Circles">
            <div className={props.updateCircle1}>1</div>
            <div className={props.updateCircle2}>2</div>
            <div className={props.updateCircle3}>3</div>
            <div className={props.updateCircle4}>4</div>
        </div>
    )
}

