import './CircleSelector.css';

export default function CircleSelector(props) {
    return (
        <div className="CircleSelector">
        <button onClick={() => props.updateBut1()} id="but1" className={props.updateClass1}>Select Circle 1</button>
        <button onClick={() => props.updateBut2()} id="but2" className={props.updateClass2}>Select Circle 2</button>
        <button onClick={() => props.updateBut3()} id="but3" className={props.updateClass3}>Select Circle 3</button>
        <button onClick={() => props.updateBut4()} id="but4" className={props.updateClass4}>Select Circle 4</button>
        </div>
    )
}