import React, { Component } from 'react';
import './App.css';
import Circles from "./components/Circles/Circles";
import CircleSelector from "./components/CircleSelector/CircleSelector";

class App extends Component {
  state = {
    but1Class: "",
    but2Class: "",
    but3Class: "",
    but4Class: "",
  }
  
  
  handleBut1 = () => {
    this.setState({
      but1Class: "selected"
    })
  }

  
  handleBut2 = () => {
    this.setState({
      but2Class: "selected"
    })
  }  
 
  handleBut3 = () => {
    this.setState({
      but3Class: "selected"
    })
  }
  
  handleBut4 = () => {
    this.setState({
      but4Class: "selected"
    })
  }


  render() {
    return (
      <div className="App">
        <header className="App-header">UNIT 4 FINAL ASSESSMENT</header>
        <main>
          
          <CircleSelector 
            updateClass1={this.state.but1Class}
            updateClass2={this.state.but2Class}
            updateClass3={this.state.but3Class}
            updateClass4={this.state.but4Class}
            updateBut1={this.handleBut1}
            updateBut2={this.handleBut2}
            updateBut3={this.handleBut3}
            updateBut4={this.handleBut4}
          />
          <Circles 
            updateCircle1={this.state.but1Class}
            updateCircle2={this.state.but2Class}
            updateCircle3={this.state.but3Class}
            updateCircle4={this.state.but4Class}
           
          />
        
        </main>
      </div>
    );
  }
}

export default App;